@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600;700&family=Raleway:wght@400;600;700&family=VT323&display=swap";
* {
  box-sizing: 0;
  margin: 0;
  padding: 0;
}

.experience {
  width: 100vw;
  height: 100vh;
  z-index: 0;
  position: fixed;
}

.experience-canvas {
  width: 100%;
  height: 100%;
}

div {
  font-family: VT323, monospace;
  font-size: larger;
}

#board {
  justify-content: center;
  align-items: center;
  display: flex;
}

#game-over {
  width: 100vw;
  height: 100vh;
  z-index: 1;
  opacity: .98;
  background-image: url("glitch.a41e0f5a.gif");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: fixed;
}

.reborn {
  width: 100vw;
  height: 100vh;
  color: azure;
  font-size: 48px;
  position: fixed;
  top: 75%;
  left: 30%;
}

.options {
  width: 15vw;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.option {
  width: 5vw;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.option:last-child {
  width: 4vw;
}

.arrow-right {
  width: 0;
  height: 0;
  border-top: 20px solid #0000;
  border-bottom: 20px solid #0000;
  border-left: 20px solid azure;
}

.snippet {
  width: 100vw;
  height: 100vh;
  z-index: 2;
  background-color: #ffd6bde6;
  position: fixed;
}

.dot-collision {
  width: 50px;
  height: 50px;
  color: #ff4882;
  background-color: #ff4882;
  border-radius: 25px;
  position: fixed;
  top: 50%;
  left: 50%;
}

.dot-collision:before, .dot-collision:after {
  content: "";
  display: inline-block;
  position: absolute;
}

.dot-collision:before {
  width: 50px;
  height: 50px;
  color: #ff4882;
  background-color: #ff4882;
  border-radius: 25px;
  animation: 2s ease-in infinite dotCollisionBefore;
  left: -50px;
}

.dot-collision:after {
  width: 50px;
  height: 50px;
  color: #ff4882;
  background-color: #ff4882;
  border-radius: 25px;
  animation: 2s ease-in 1s infinite dotCollisionAfter;
  left: 50px;
}

@keyframes dotCollisionBefore {
  0%, 50%, 75%, 100% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(-25px);
  }
}

@keyframes dotCollisionAfter {
  0%, 50%, 75%, 100% {
    transform: translateX(0);
  }

  25% {
    transform: translateX(15px);
  }
}

.dot-flashing {
  width: 50px;
  height: 50px;
  color: #ff4882;
  background-color: #ff4882;
  border-radius: 25px;
  animation: 1s linear .5s infinite alternate dotFlashing;
  position: fixed;
  top: 50%;
  left: 50%;
}

.dot-flashing:before, .dot-flashing:after {
  content: "";
  display: inline-block;
  position: absolute;
  top: 0;
}

.dot-flashing:before {
  width: 50px;
  height: 50px;
  color: #ff4882;
  background-color: #ff4882;
  border-radius: 25px;
  animation: 1s infinite alternate dotFlashing;
  left: -75px;
}

.dot-flashing:after {
  width: 50px;
  height: 50px;
  color: #ff4882;
  background-color: #ff4882;
  border-radius: 25px;
  animation: 1s 1s infinite alternate dotFlashing;
  left: 75px;
}

@keyframes dotFlashing {
  0% {
    background-color: #ff4882;
  }

  50%, 100% {
    background-color: #f9cddc;
  }
}

.text {
  width: 75%;
  color: #ff4882;
  border: 6px solid #ff4882;
  border-radius: 1.5rem;
  padding: 3rem;
  font-size: 4rem;
  position: absolute;
  top: 25vh;
  left: 10vw;
}

#start {
  width: 100vw;
  height: 100vh;
  z-index: 3;
  background-color: #ffd6bde6;
  position: fixed;
}

#play-btn {
  color: azure;
  background-color: #ff4882;
  border: .5px solid #fffdfd00;
  border-radius: 2.5rem;
  margin-top: 1rem;
  padding: 1.5rem;
  font-family: VT323, monospace;
  font-size: 2rem;
  position: relative;
  left: 50%;
}

#play-btn:hover {
  background-color: #c0305d;
}

#play-btn:active {
  background-color: #c0305d;
  transform: translateY(4px);
}

#end-game {
  width: 100vw;
  height: 100vh;
  background-color: #000;
  position: fixed;
}

.end-text {
  color: #fff;
  width: 75%;
  border: 6px solid #fff;
  border-radius: 1.5rem;
  padding: 3rem;
  font-size: 4rem;
  position: fixed;
  top: 25vh;
  left: 10vw;
}

.end-main {
  background-image: linear-gradient(to left, violet, indigo, #00f, green, #ff0, violet, red);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: #fff;
  margin-left: auto;
  margin-right: auto;
  font-size: 8.5rem;
  position: relative;
  left: 25vw;
}

